.popup{
  width: 100%;
  height: 100%;
  background-color:rgba(0,0,0, .5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility .3s, opacity .3s linear;
}


